'use client';

import React, { useState } from 'react';
import useSWR from 'swr';
import {
    Search, Plus, Minus, ShoppingCart, Trash2,
    Utensils, Coffee, Pizza, CreditCard, Banknote, QrCode
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

interface Product {
    id: string;
    name: string;
    category: string;
    price: number;
    stockQty: number;
    imageUrl?: string;
}

interface CartItem extends Product {
    qty: number;
}

export default function POSPage() {
    const { data: productsData } = useSWR('/api/products', fetcher);
    const products: Product[] = productsData?.data || [];

    const [activeCategory, setActiveCategory] = useState('ALL');
    const [search, setSearch] = useState('');
    const [cart, setCart] = useState<CartItem[]>([]);
    const [isCheckingOut, setIsCheckingOut] = useState(false);

    // Filter Products
    const filteredProducts = products.filter(p => {
        const matchCat = activeCategory === 'ALL' || p.category === activeCategory;
        const matchSearch = p.name.toLowerCase().includes(search.toLowerCase());
        return matchCat && matchSearch;
    });

    // Cart Logic
    const addToCart = (product: Product) => {
        setCart(prev => {
            const existing = prev.find(p => p.id === product.id);
            if (existing) {
                return prev.map(p => p.id === product.id ? { ...p, qty: p.qty + 1 } : p);
            }
            return [...prev, { ...product, qty: 1 }];
        });
    };

    const updateQty = (id: string, delta: number) => {
        setCart(prev => prev.map(p => {
            if (p.id === id) return { ...p, qty: Math.max(0, p.qty + delta) };
            return p;
        }).filter(p => p.qty > 0));
    };

    const subtotal = cart.reduce((sum, item) => sum + (Number(item.price) * item.qty), 0);
    const tax = subtotal * 0.11;
    const total = subtotal + tax;

    const handleCheckout = async (method: string) => {
        if (cart.length === 0) return;
        setIsCheckingOut(true);

        const payload = {
            paymentMethod: method,
            items: cart.map(item => ({
                id: item.id,
                name: item.name,
                price: Number(item.price),
                qty: item.qty,
                type: 'PRODUCT'
            }))
        };

        try {
            const res = await fetch('/api/transactions', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload)
            });

            if (res.ok) {
                alert('Transaction Successful!');
                setCart([]);
            } else {
                alert('Transaction Failed');
            }
        } catch (e) {
            alert('Error processing transaction');
        } finally {
            setIsCheckingOut(false);
        }
    };

    return (
        <div className="flex flex-1 overflow-hidden h-full bg-[#050a07]">
            {/* Main Content (Menu) */}
            <div className="flex-1 flex flex-col p-6 min-w-0">
                {/* Top Bar */}
                <div className="flex justify-between items-center mb-6">
                    <div className="flex gap-2">
                        {['ALL', 'FOOD', 'DRINK', 'SNACK'].map(cat => (
                            <button
                                key={cat}
                                onClick={() => setActiveCategory(cat)}
                                className={`px-4 py-2 rounded-lg text-sm font-bold transition-colors
                            ${activeCategory === cat ? 'bg-[#22c55e] text-black' : 'bg-[#16261d] text-slate-400 hover:text-white'}
                        `}
                            >
                                {cat}
                            </button>
                        ))}
                    </div>
                    <div className="relative w-64">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-500" size={18} />
                        <input
                            type="text"
                            placeholder="Search menu..."
                            className="w-full bg-[#16261d] border border-[#1e3328] rounded-full py-2 pl-10 pr-4 text-sm text-white focus:outline-none focus:border-[#22c55e]"
                            value={search}
                            onChange={e => setSearch(e.target.value)}
                        />
                    </div>
                </div>

                {/* Grid */}
                <div className="grid grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 overflow-y-auto pb-20">
                    {filteredProducts.map(product => (
                        <div key={product.id} className="bg-[#0f1a14] border border-[#1e3328] rounded-xl overflow-hidden group hover:border-[#22c55e]/50 transition-all flex flex-col">
                            <div className="aspect-video bg-slate-800 bg-cover bg-center relative" style={{ backgroundImage: `url(${product.imageUrl})` }}>
                                <button
                                    onClick={() => addToCart(product)}
                                    className="absolute inset-0 bg-black/40 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity"
                                >
                                    <div className="bg-[#22c55e] text-black p-2 rounded-full transform scale-0 group-hover:scale-100 transition-transform">
                                        <Plus size={24} />
                                    </div>
                                </button>
                            </div>
                            <div className="p-4 flex-1 flex flex-col">
                                <h4 className="font-bold text-white mb-1 truncate">{product.name}</h4>
                                <p className="text-sm text-[#22c55e] font-mono mb-4">Rp {Number(product.price).toLocaleString()}</p>
                                <div className="mt-auto flex justify-between items-center text-xs text-slate-500">
                                    <span>Stock: {product.stockQty}</span>
                                    <span className="px-2 py-0.5 rounded bg-slate-800 border border-slate-700 text-[10px]">{product.category}</span>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>

            {/* Right Sidebar (Cart) */}
            <div className="w-[400px] bg-[#0f1a14] border-l border-[#1e3328] flex flex-col z-10 shadow-xl">
                <div className="p-6 border-b border-[#1e3328] flex items-center justify-between">
                    <h2 className="text-xl font-bold text-white flex items-center gap-2">
                        <ShoppingCart className="text-[#22c55e]" /> Current Order
                    </h2>
                    <span className="bg-[#22c55e]/20 text-[#22c55e] text-xs font-mono px-2 py-1 rounded">
                        {cart.reduce((a, b) => a + b.qty, 0)} Items
                    </span>
                </div>

                <div className="flex-1 overflow-y-auto p-4 space-y-3">
                    {cart.length === 0 ? (
                        <div className="h-full flex flex-col items-center justify-center text-slate-500 opacity-50">
                            <ShoppingCart size={48} className="mb-4" />
                            <p>Cart is empty</p>
                        </div>
                    ) : (
                        cart.map(item => (
                            <div key={item.id} className="bg-[#16261d] border border-[#1e3328] p-3 rounded-lg flex gap-3">
                                {/* Image thumb */}
                                <div className="size-16 bg-slate-800 rounded bg-cover bg-center" style={{ backgroundImage: `url(${item.imageUrl})` }}></div>
                                <div className="flex-1 min-w-0 flex flex-col justify-between">
                                    <div className="flex justify-between items-start">
                                        <h4 className="font-bold text-white text-sm truncate">{item.name}</h4>
                                        <p className="text-[#22c55e] text-xs font-mono">{(Number(item.price) * item.qty).toLocaleString()}</p>
                                    </div>
                                    <div className="flex items-center gap-3">
                                        <button onClick={() => updateQty(item.id, -1)} className="p-1 hover:text-white text-slate-400"><Minus size={14} /></button>
                                        <span className="text-sm font-bold w-4 text-center text-white">{item.qty}</span>
                                        <button onClick={() => updateQty(item.id, 1)} className="p-1 hover:text-white text-[#22c55e]"><Plus size={14} /></button>
                                    </div>
                                </div>
                            </div>
                        ))
                    )}
                </div>

                <div className="p-6 bg-[#16261d] border-t border-[#1e3328]">
                    <div className="space-y-2 mb-6 text-sm">
                        <div className="flex justify-between text-slate-400">
                            <span>Subtotal</span>
                            <span>{subtotal.toLocaleString()}</span>
                        </div>
                        <div className="flex justify-between text-slate-400">
                            <span>Tax (11%)</span>
                            <span>{tax.toLocaleString()}</span>
                        </div>
                        <div className="flex justify-between text-white text-lg font-bold border-t border-[#1e3328] pt-2 mt-2">
                            <span>Total</span>
                            <span className="text-[#22c55e]">{total.toLocaleString()}</span>
                        </div>
                    </div>

                    <div className="grid grid-cols-2 gap-3 mb-3">
                        <button
                            onClick={() => handleCheckout('CASH')}
                            disabled={cart.length === 0 || isCheckingOut}
                            className="flex items-center justify-center gap-2 py-3 bg-[#1e3328] hover:bg-[#253d30] text-white rounded font-bold border border-[#22c55e]/30 transition-colors disabled:opacity-50"
                        >
                            <Banknote size={18} /> Cash
                        </button>
                        <button
                            onClick={() => handleCheckout('QRIS')}
                            disabled={cart.length === 0 || isCheckingOut}
                            className="flex items-center justify-center gap-2 py-3 bg-[#1e3328] hover:bg-[#253d30] text-white rounded font-bold border border-[#22c55e]/30 transition-colors disabled:opacity-50"
                        >
                            <QrCode size={18} /> QRIS
                        </button>
                    </div>
                    <button
                        onClick={() => handleCheckout('CASH')}
                        disabled={cart.length === 0 || isCheckingOut}
                        className="w-full py-4 bg-[#22c55e] hover:bg-[#16a34a] text-black font-bold rounded-lg shadow-lg shadow-[#22c55e]/20 transition-all active:scale-[0.98] disabled:opacity-50 disabled:active:scale-100"
                    >
                        {isCheckingOut ? 'Processing...' : 'CHARGE Rp ' + total.toLocaleString()}
                    </button>
                </div>
            </div>
        </div>
    );
}
